/*For all the devices which are 1200px or more than it*/
.Profile__container{
    margin-top: 147px;
    padding-left: 97px;
}
.Profile__photo{
    width: 190px;
    height:190px;
    border-radius: 80px;
    box-sizing: border-box;
    border: 4px solid #FFFFFF;
    box-shadow: 0px 2px 7px #CC9B66;
    float: left;
}
.Profile__name{
  position: absolute;
  left: 225px;
  top: 51px;
}
.Profile__myself{
    font-size: 28px;
    line-height: 33px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-style: normal;
    font-weight: 600;
    color: #4A90E2;
}
.Profile__firstname{
    font-size: 22px;
    line-height: 26.25px;
    font-weight: 600;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-style: normal;
    color: #291E17;
    margin-top: 15px;
}
.Profile__gender{
    font-weight: 600;
}
.Profile__age{
    font-size: 18px;
    font-weight: 300;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    line-height: 21.48px;
}
.Profile__city{
    font-size: 18px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.Profile__family-details-row1{
    position: absolute;
    top:258px;
    left: 24px;
}
.Profile__family-details-row2{
    position: absolute;
    top: 399.94px;
    left: 24px;
}
.Profile__left-characters{
    float:left;
    width: 60.94px;
    height: 60.94px;
    border: 2.1px solid #FFFFFF;
    border-radius: 51px;
    box-sizing: border-box;
    box-shadow: 0px 2px 7px #CC9B66;
}
.Profile__right-characters{
    float: right;
    margin-left: 50px;
    width: 60.94px;
    height: 60.94px;
    border: 2.1px solid #FFFFFF;
    border-radius: 51px;
    box-sizing: border-box;
    box-shadow: 0px 2px 7px #CC9B66;
}
.Profile__span-leftnames{
    float: left;
    position: absolute;
    left: 0px;
    top: 70px;
    font-size: 18px;
}
.Profile__span-rightnames{
    float: right;
    position: absolute;
    right: 9px;
    top: 70px;
    font-size: 18px;
}
.Profile__icon{
    position: absolute;
    top: 541px;
    left: 35px;
}
.Profile__plus{
    position: absolute;
    left:11px;
    top:11px;
}
/*React Tabs css properties*/

.react-tabs__tab-list{
    border-bottom: 0 !important;
    padding: 0;
    margin:0px 0px 86px 0px!important;
    font-size: 31px !important;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.react-tabs__tab{
    display: inline-block;
    border: none !important;
    border-bottom: none !important;
    bottom: 0px !important;
    position: relative;
    list-style: none !important;
    padding: 0px 0px 0px 0px !important;
    margin: 46px 204px 0px 0px !important;
    cursor: pointer;
}
.react-tabs__tab--selected{
    border-color: #CC9B66 !important;
    color:black;
    border-radius: 0px 0px 0px 0px !important;
    border-bottom: 6px solid #CC9B66 !important ;
    font-weight: bold !important;
}
/*For small and medium devices*/
@media (min-width:576px) and (max-width:800px){
    .Profile__photo{
        width: 140px;
        height: 140px;
        border-radius: 60px;
    }
    .Profile__left-characters{
        float: left;
        width:50.94px;
        height:50.94px;
        border-radius: 50px;
    }
    .Profile__right-characters{
        float: right;
        margin-left: 44px;
        width: 50.94px;
        height: 50.94px;
        border-radius:50px;
    }
    .react-tabs__tab{
        margin: 46px 100px 0px 0px !important;
        font-size: 25px;
    }
    .Profile__container{
        margin-top: 147px;
        padding-left: 50px;
    }
    .Profile__name{
        position: absolute;
        left:177px;
        top:50px;
    }
    .Profile__myself{
        font-size:20px;
        line-height:20px;
    }
    .Profile__firstname{
        font-size:16px;
        line-height:22.25px;
        width: max-content;
        margin-top:5px;
    }
    .Profile__age{
        font-size: 16px;
    }
    .Profile__city{
        font-size:16px;
    }
   .react-tabs__tab-list{
       margin: 0px 0px 86px 95px !important;
   }
   .react-tabs__tab{
       margin: 45px 85px 0px 0px !important;
       font-size: 22px;
   }
}

/*For devices for medium and large devices*/
@media(min-width: 800px) and (max-width:1200px){
   .Profile__photo{
       width: 180px;
       width: 180px;
   }
   .Profile__container{
       padding-left: 50px;
   }
   .Profile_myself{
       font-size: 22px;
   }
   .Profile__firstname{
       font-size: 16px;
       line-height: 21.25px;
       width:max-content;
   }
   .Profile__age{
       font-size: 16px;
   }
   .Profile__city{
       font-size: 16px;
   }
   .react-tabs__tab{
       margin: 46px 130px 0px 0px !important;
       font-size: 28px;
   }
   .react-tabs__tab-list{
       margin: 0px 0px 86px 70px !important;
   }
}
